.project-heading{
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    padding-bottom: 3.5rem;
}

.project-card{
    background: #1a1919;
    padding: 1.2rem 1rem;   
}

.project-card img{
    width: 100%;
}

.project-title
{
    color: #ffea00;
    padding: 1rem 0;
    font-weight: 500;
    
}

.pro-details p{
    padding-bottom: 2rem;
    font-size: 1.1rem;
    font-weight: 100;
    
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.pro-btns{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0s;
    padding-left: 25%;
    align-self: center;
}

.pro-btns .btn{
    padding: 0.5rem 1rem;
}


@media screen and (max-width: 740px) {
    .project-container{
        max-width: 90%;
        margin: auto;

        grid-template-columns: 1fr;
    }
    
}