.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    padding-left: 5rem;
    padding-right: 5rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
    color: transparent;
    position: fixed;
}



.nav-menu{
    display: flex;
}

.nav-menu li{
    padding: 0 1rem;
}

.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger{
    display: none;
}



